.card {
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.5);
  border-radius: 1%;
  transition: 0.5s;
  /*height: -webkit-fill-available;*/
}

.card:hover {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
  border-radius: 1%;
  transform: scale(1.02);
}
.card .menu-titel a {
  color: #444;
  font-weight: 600;
}
.card-footer {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.card-footer .btn {
  font-size: 1em;
  color: white;
  background: #ff0157;
  display: inline-block;
  padding: 10px 20px;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  transition: 0.6s;
  /**/
}
.card-footer .btn a {
  color: white;
}

.card-footer .btn:hover {
  color: white;
  font-weight: bold;
  letter-spacing: 4px;
}

.headersc {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 40px 90px;
  z-index: 99;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: 0.5s;
}

.headersc .logo {
  color: white;
  font-weight: bold;
  font-size: 2em;
  text-decoration: none;
}

.headersc .logo span {
  color: #ed1432;
}

.headersc .navigation {
  position: relative;
  display: flex;
}

.headersc .navigation li {
  margin-left: 30px;
}

.headersc .navigation li a {
  color: white;
  font-weight: normal;
  transition: 0.5s;
}

.headersc.fixednav {
  background-color: white;
  padding: 08px 120px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.5);
}

.headersc.fixednav .logo {
  color: black;
}

.headersc.fixednav .navigation li a {
  color: black;
  font-weight: normal;
}

.headersc .navigation li a:hover {
  color: #ed1432;
  font-weight: bolder;
  letter-spacing: 2px;
}

