@media (max-width: 576px) {
  /* Header 
  ----------------------------------------*/
  .headersc {
    padding: 40px 25px;
  }
  .headersc.fixednav {
    padding: 08px 35px;
    z-index: 9999999;
  }
  .headersc .navigation {
    display: none;
    padding: 0;
    margin: 0;
    opacity: 0;
    transition: all 3s ease;
  }
  .headersc .navigation.active {
    position: fixed;
    width: 100%;
    height: calc(100% -80px);
    top: 85px;
    right: 0%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    background: white !important;
    border-radius: 0 0 4% 4%;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.6);
    z-index: 10;
