    margin-right: auto;
    padding: 5%;
  }
  .headersc .navigation li a {
    color: black !important;
    font-size: 1.7rem;
    padding: 0;
    margin: 0;
  }
  .toggel {
    position: relative;
    width: 50px;
    height: 45px;
    background: url(data:image/png;base64,);
    background-size: 40px;
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
  }
  .toggel.active {
    background: url(/react-recipe-app/static/media/togleclosew.9a196f20.png);
    background-size: 35px;
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
  }
  .headersc.fixednav .toggel {
    -webkit-filter: invert(1);
            filter: invert(1);
