.expert-items .expert-item .expert-border-item .expert-titel a {
  color: #444;
  font-weight: 600;
}

/*  social icons*/

.expert-social {
  padding: 0;
  height: 2rem;
  position: relative;
}

.expert-social a {
  display: inline-block;
  width: 40px;
  /* height: 4x;*/
  line-height: 40px;
  font-size: 30px;
  text-align: center;
  background-color: white;
  color: #32353c;
  margin: 0 1vw;
}

.expert-social li {
  display: inline-block;
  padding: 0;
  margin: 0 5px 0 0;
  /*line-height: normal;*/
  transform: translateY(50px);
  opacity: 0;
  transition: 0.6s;
}

.expert-items .expert-item :hover .expert-social li {
  transform: translateY(-5px);
  opacity: 1;
}

.expert-social a:hover {
  font-size: 33px;
}

/* background color for ech icon */

.rounded-circle {
  transition: 0.8s;
}

.faicon-facebook:hover {
  color: #3b5998 !important;
  transform: rotatez(720deg);
}

.faicon-twitter:hover {
  color: #1da1f2 !important;
  transform: rotatez(-720deg);
}

.faicon-instagram:hover {
  color: #c13584 !important;
  transform: rotatez(720deg);
}

.faicon-linkedin:hover {
  color: #2867b2 !important;
  transform: rotatez(-720deg);
}

