/**/

.titel-menu {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.titel-menu h2 {
  font-size: 2.5rem;
  font-weight: bold;
  color: #444;
}

.titel-menu p {
  font-size: 1.3rem;
  font-weight: normal;
  color: #555;
}

.titel-menu span {
  font-family: "Sansita Swashed", cursive;
  font-size: 3rem;
  font-weight: bold;
  color: #ff0157;
}

/*btn*/

.contactbtn {
  font-size: 1em;
  color: white;
  background: #ff0157;
  display: inline-block;
  padding: 10px 30px;
  margin-top: 35px;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  transition: 0.6s;
}

.contactbtn:hover {
  color: white;
  font-weight: bold;
  letter-spacing: 2.5px;
}

